*{
  padding: 0;
  margin: 0;
  box-sizing: content-box;
}
body{
  background-color: #d1ddf3 ;  
  font-family: 'Roboto', sans-serif;
  color: #273c75;
  
}

.borderUniv{
  border:1px solid black
}


section{
  padding: 0px 10em;
}

#navbar-box{
  height: 5em;
  display: flex;
  flex-wrap: wrap;
  padding: 0px 10em;
  justify-content: space-between;
  align-items: center;
  -webkit-box-shadow: 0px 24px 19px -28px rgba(47,54,64,0.48);
  -moz-box-shadow: 0px 24px 19px -28px rgba(47,54,64,0.48);
  box-shadow: 0px 24px 19px -28px rgba(47,54,64,0.48)
}


#logo{  
  /* background-color: green;   */
  letter-spacing: 3px;
  height: 2em;
  width: 7em;
  font-weight:700;
  font-size: 2em ;
  border-radius: 20px;
}
#logout{  
  cursor: pointer;
  border-radius: 10px;
  width: 3.5em;
  height: 3.5em;
  display: flex;
  justify-content:center;
  align-items:center;
  background: #d1ddf3 ;
  border-radius: 10px;
  background: #d1ddf3 ;
  box-shadow:  5px 5px 11px #c7d2e7, 
             -5px -5px 11px #dbe8ff;
             
}

#logout:hover{
  color:#777575;
  border-radius: 10px;
  background: #d1ddf3 ;
  box-shadow: inset 5px 5px 11px #c7d2e7, 
            inset -5px -5px 11px #dbe8ff;
}

#addBox{
  padding-top: 2em;
  display: flex;  
  justify-content:center;
  align-items:center;
}

#addBtn{
  cursor: pointer;
  border-radius: 50%;
  width: 3.5em;
  height: 3.5em;
  display: flex;
  justify-content:center;
  align-items:center;
  background: #d1ddf3 ;
  box-shadow:  5px 5px 14px #bac5d8, 
               -5px -5px 14px #e8f5ff;
}

#addBtn:hover{
  color:#777575;
  border-radius: 50%;
  background: #d1ddf3 ;
  box-shadow: inset 5px 5px 14px #bac5d8, 
              inset -5px -5px 14px #e8f5ff;
}

#cardContainer{
  margin-top: 1em;
  height: 75vh;
  display: flex;
  flex-wrap: wrap;
  /* align-items: center; */
  justify-content: center;
}

.cardBox{
  /* border: 1px solid red; */
  border-radius: 2em;
  width: 20em;
  margin: 2em;
  -webkit-box-shadow: 0px -2px 27px -10px rgba(47,54,64,0.52);
  -moz-box-shadow: 0px -2px 27px -10px rgba(47,54,64,0.52);
  box-shadow: 0px -2px 27px -10px rgba(47,54,64,0.52);
}

.headCard{
  height: 70px;
  padding: 0px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  -webkit-box-shadow: 1px 10px 12px -9px rgba(47,54,64,0.32);
-moz-box-shadow: 1px 10px 12px -9px rgba(47,54,64,0.32);
box-shadow: 1px 10px 12px -9px rgba(47,54,64,0.32);
}

.nameCard{
font-size: 1.2em;
font-weight: 500;
letter-spacing: 3px;
}

.cardContainer{
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  height: 55vh;
  padding: 1em;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.cards{
  margin-top: 1em;
  height: 10em;
  padding: 1em;
  border-radius: 1.5em;
  display: flex;
  flex-direction: column;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(39,60,117,0.3);
-moz-box-shadow: 0px 0px 10px 0px rgba(39,60,117,0.3);
box-shadow: 0px 0px 10px 0px rgba(39,60,117,0.3);

}

.headCardBox{
  display: flex;
  justify-content: space-between;
}
.status{
  font-size: 0.6em;
  letter-spacing: 0.4em;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1em;
  width: 9em;
  background-color: #273c75;
  color: #fff;
}
.iconEdit{
  display: flex;
  align-items: center;
  padding: 0.1em;
  cursor: pointer;
  border-radius: 2px;
  background: #d1ddf3 ;
box-shadow:  3px 3px 6px #b6c0d3, 
-3px -3px 6px #ecfaff;
}

.iconEdit:hover{
  border-radius: 2px;
  color: #777575;
  background: #d1ddf3 ;
  box-shadow: inset 3px 3px 6px #b6c0d3, 
              inset -3px -3px 6px #ecfaff;
}
.titleCards{
  margin-top: 1em;
  font-size: 0.9em;
  font-weight: 500;
}
.descriptionCards{
  margin-top: 0.5em;
  height: 4.5em;
  font-size: 0.8em
}

.switchCards{
  /* margin: 0.8em; */
  display: flex;
  justify-content: space-between;
}
.iconNextPrev{
  display: flex;
  justify-content: space-between;
}
.delete .feather{
  width: 16px;
  height: 16px;
}

.delete{
  cursor: pointer;  
  padding: 5px;
  border-radius: 5px;
background: #d1ddf3 ;
box-shadow:  3px 3px 6px #b6c0d3, 
             -3px -3px 6px #ecfaff;
}
.prev,.next{
  margin-right: 15px;
  cursor: pointer;  
  padding: 5px;
  border-radius: 5px;
background: #d1ddf3 ;
box-shadow:  3px 3px 6px #b6c0d3, 
             -3px -3px 6px #ecfaff;
}
.prev:hover,.next:hover{
  color: #777575;
  border-radius: 6px;
  background: #d1ddf3 ;
  box-shadow: inset 3px 3px 6px #b6c0d3, 
              inset -3px -3px 6px #ecfaff;
}
.delete:hover{
  border-radius: 5px;
  color: #777575;
background: #d1ddf3 ;
box-shadow: inset 3px 3px 6px #b6c0d3, 
            inset -3px -3px 6px #ecfaff;
}
.delete .feather{
  padding: 5px;
  width: 16px;
  height: 16px;
}

.prev .feather{
  padding: 5px;
  width: 16px;  
  height: 16px;
}
.next .feather{
  padding: 5px;
  width: 16px;  
  height: 16px;
}

/* ********** LOGIN ********** */
.loginRegisPage{
  height: 100vh;
  display: flex;
  justify-content: center;
}
.loginRegisBox{
  padding: 10%;
  display: flex;
  flex-direction: column;
  text-align: center;
  
}
input{
  outline: none;
  text-align: center;
  background: transparent;
  border: none;  
    padding: 10px;
    
}
.formInputLoginRegis{
margin:1em 0px
}
.formInputLoginRegis{
  padding: 2px;
border-radius: 5px;
width: 20em;
height: 40px;
border-radius: 20px;
background: #d1ddf3;
box-shadow:  3px 3px 6px #b2bccf, 
             -3px -3px 6px #f0feff;
}
.formInputLoginRegis:hover{
  border-radius: 20px;
background: #d1ddf3;
box-shadow: inset 3px 3px 6px #b2bccf, 
            inset -3px -3px 6px #f0feff;
}
#googleLogin{
  height: 40px;
  text-align: center;
  margin: 1em;
  background-image: url('../asset/btn_google_signin_dark_normal_web.png');
  background-repeat: no-repeat;
  background-position: center;
}
#googleLogin:hover{
  background-image: url('../asset/btn_google_signin_dark_pressed_web.png');
}
.boxBtnloginRegister{
  display: flex;
  /* border: 1px solid black; */
}
.btnLoginRegister{
  /* border: 1px solid black; */
  padding: 5px 20px;
  border-radius: 20px;
  letter-spacing: 2px;
  font-weight: 500;
  font-size: 0.9em;
  margin-right: 10px;
}
.morBtnLoginRegis{
/* border-radius: 20px; */
background: #d1ddf3;
box-shadow:  5px 5px 9px #b2bccf, 
             -5px -5px 9px #f0feff;
}
.morBtnLoginRegis:hover{
  color: #777575;
  border-radius: 20px;
background: #d1ddf3;
box-shadow: inset 5px 5px 9px #b2bccf, 
            inset -5px -5px 9px #f0feff;
}